@media screen and (max-width: 768px) {
  #top, #bottom, #left, #right{
    display: none;
  }
  section:not(.home),h1{
  margin:0 auto;
  padding:0;
  /* display:block; */
  height: 100%;
  width:100%;
  padding-top:30%;
}
h1{
  font-size: 3em;  
  padding:0;
}
.divider{
  display:none;
}
.trademark{
  display: none;
}
.full{
    display: flex;
    flex-wrap: wrap;
    margin:0;
    padding:0;
    padding-top:30%;
}
.full>a{
width: 100%;
}
.welcome{
    display:none;
}
.logo{
    background-color:deeppink;
}
.iconsnav{
  width:fit-content;
  background-color: rgba(0, 0, 0, 0.4);
}
.iconsnav, .navTop{
    position: fixed;
    top:0;
    margin:0;
    z-index:1;
}
.iconsnav img{
    width: 15%;
    height: 15%;
  }
.iconsnav a:last-child{
  font-size: 1.2em;
  }
.navTop{
  background-color: grey;
  width: 100%;
  padding: 5px;
  justify-content: space-evenly;
}
.navTop a{
  float:right;
}
.navTop img{
  font-size: 18px;
  margin:0;
}
.back-arrow::before{
  top: -16px;
  left:-50%;
  font-size: 5em;
}
.aboutbox-article{
  display: block;
  margin:0;
}
.aboutbox p{
  padding: 10px;
  margin: 0;
}
.date{
  display: none;
}
.projects li:nth-child(6) {
      font-size: 2em;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      padding: 30px;
    }
    .projects li a {
      padding: 15px;
      border: 2px solid white;
      border-radius: 50%;
      width: 80px;
      height: 80px;
      display: grid;
      place-content: center;
    }
    ul span {
      display: none;
    }
  .button i{
    font-size: 3em;
  }
  .collab{
    display:none;
  }
  .contact{
    display:none;
  }
  }