.navTop {
    margin: 50px;
    display: flex;
    position: relative;
    background-color: transparent;
    justify-content: flex-end;
    text-align: center;
  }
  .back-arrow::before{
    position: absolute;
    content:"<";
    color: gold;
    font-family: "Amatic SC", cursive;
    font-size: 6em;
    right: 38%;
    top: -53px;
  }
  .navTop a {
    padding: 0 3% 0 3%;
  }
  .navTop img{
    height:3em;
  }
  .back-arrow:hover:before{
    color: deeppink;
  }
  .navTop img:hover {
    filter: invert(39%) sepia(90%) saturate(7374%) hue-rotate(317deg) brightness(105%) contrast(101%);
  }