@import url("https://fonts.googleapis.com/css?family=Josefin+Slab|Fredericka+the+Great|Indie+Flower|Caveat|Lobster|Quicksand|Amatic+SC&display=swap");
@import url("../src/css/home.css");
@import url("../src/css/about.css");
@import url("../src/css/projects.css");
@import url("../src/css/contact.css");
@import url("../src/css/navTop.css");
@import url("../src/css/responsive.css");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(images/background.jpg) no-repeat center center fixed;
  height: 100vh;
  width: 100%;
}
/* border page */
#top, #bottom, #left, #right {
  background: whitesmoke;
  position: fixed;
  z-index:1;
}
#left, #right {
    top: 0; bottom: 0;
		width: 5px;
  }
  #left { left: 0; }
  #right { right: 0; }
  
	#top, #bottom {
    left: 0; right: 0;
		height: 5px;
  }
  #top { top: 0; }
  #bottom { bottom: 0; }
  /* border page end*/

  section:not(.home) {
    width: inherit; 
    height: 100%;
   margin: 0 50px 50px 50px;
   padding: 70px 50px 50px 50px;
   background-color: rgba(0, 0, 0, 0.4);
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  }
  .content{
    width: 100%;
    margin-top:70px;
  }
  .divider{
    width: 111px;
    display: block;
    border: none;
    border-bottom: 5px solid aquamarine;
    margin:10px auto;  
  }
  h1 {
    font-size: 3em;
  color: white;
  text-align:center;
  font-weight:100;
  font-family: 'Fredericka the Great', cursive;
}
h3,
p,
span,
article{
  color: white;
}
/* animation */

.puff-in-center {
	-webkit-animation: puff-in-center 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
	        animation: puff-in-center 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
}
/* keyframes */

@-webkit-keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}
@keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}

