#about{
  display: flow-root;
}
.aboutbox article {
  font-family: 'Fredericka the Great', cursive;
  border-bottom: 1px solid gold;
  text-align: justify;
  padding-bottom: 50px; 
}
.aboutbox>article:not(.bio){
  padding-top: 50px;
}
.bio .date{
float:left;
}
.skills .date{
float:right;
}
.skills>p span{
  color:gold;
}
.date span {
  font-family: "Lobster", cursive;
  font-size: 3em;
  line-height: 90px;
  color: white;
}
.date {
  border: 4px gold solid;
  shape-outside: circle(95px);
  width: 90px;
  height: 90px;
  border-radius: 90px;
  text-align:center;
  margin: 50px;
  /* background-color: gold; */
}

