.home{
  overflow: hidden;
  width: 100%; 
  height: 95vh;
  font-family: 'Fredericka the Great', cursive;
}
.full {
  height: 100%;
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  padding: 0 0 20px 20px;
}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid; */
  align-self:center;
  text-align: center;
  color: #ff1493;
  font-size:50px;
  margin:10px;
}
.full > a:hover{
  color: #ffd700;
  transition: color 2s;
}
.aboutme {
  grid-area: 1 / 1 / 4 / 1;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  background-image: url(../images/final824-roses-drawing.svg);
  background-size: auto;
  background-position: left;
  display: grid;
  place-items: center;
  text-shadow: 2px 2px black;
}
.welcome p {
  font-size: 3em;
  text-align: right;
  border-right: 5px dotted aquamarine;
  padding-right:20px;
  margin:20px;
}
.welcome p:hover{
  color: #7fffd4;
  transition: color 2s;
}
.logo{
  display: none;
}
.iconsnav {
  grid-area: 4 / 1 / 4 / 4;
}
.iconsnav a{
  padding: 0 10px 0 10px;
}
.iconsnav img{
  height: 3em;
}
.iconsnav a:last-child{
  font-weight:400;
  color:#FFD700;
  font-size: 1.5em;
}
.iconsnav a:last-child:hover{
  color: deeppink;
}
.iconsnav img:hover {
  filter: invert(39%) sepia(90%) saturate(7374%) hue-rotate(317deg) brightness(105%) contrast(101%);
}
