.projects {
  font-family: "Quicksand", sans-serif !important;
}
.projectbox{
  text-align:center;
  margin-top:70px;
}
.projectbox img {
margin-bottom: 30px;
max-width: 100%;
max-height: 100%;
}
h2 {
  font-size: 2em;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  color: gold;
  word-break:break-word;
}
.projectbox li {
  padding-bottom: 5px;
}
.projectbox li:nth-child(6) {
  font-size: 1.5em;
  padding-top: 15px;
  border-bottom: 1px solid gold;
}
.button i {
  padding: 15px;
}
.button span {
  padding-left: 2%;
}
.button:hover > span,
i {
  color: gold;
  border: gold;
}